#Portfolio * {
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out; }

#Portfolio .project:not(.opened) .image-info {
  background: #000;
  overflow: hidden;
  cursor: pointer; }
  #Portfolio .project:not(.opened) .image-info:hover img {
    opacity: .6;
    -webkit-transform: scale(1.05);
    -ms-transform: scale(1.05);
    transform: scale(1.05); }
  #Portfolio .project:not(.opened) .image-info:hover .info {
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    transform: translate(0, 0); }
  #Portfolio .project:not(.opened) .image-info .info {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-transform: translate(0, 100%);
    -ms-transform: translate(0, 100%);
    transform: translate(0, 100%); }

#Portfolio .project.opened {
  -webkit-box-shadow: 3px 3px 40px 0px #212121;
  box-shadow: 3px 3px 40px 0px #212121; }
  #Portfolio .project.opened > div:first-child {
    padding: 0; }
    #Portfolio .project.opened > div:first-child .info {
      display: none; }
  #Portfolio .project.opened .all-info {
    opacity: 1;
    -webkit-transition-delay: .6s;
    -o-transition-delay: .6s;
    transition-delay: .6s; }

#Portfolio .all-info {
  position: absolute;
  top: 0;
  bottom: 0;
  opacity: 0;
  -webkit-transition-delay: 0s;
  -o-transition-delay: 0s;
  transition-delay: 0s; }

#Counter {
  position: relative;
  margin-top: 8%;
  padding: 5% 0; }
  #Counter h1 {
    font-size: 6rem; }
  #Counter:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -2;
    background-image: url(./bg.png);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.5); }



